import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from './index.vue'
import Jz from './jz.vue'
import Ys from './ys.vue'
import Yes from './yes.vue'
import Bm from './bm.vue'
import Gywm from './gywm.vue'
import Dl from './dl.vue'
import gwc from './gwc.vue'
Vue.use(VueRouter)
var routes = [
{ 
path:'/',
component:Index,
},{ 
path:'/2',
component:Jz,
},{
	path:'/3',
	component:Ys,
},
,{
	path:'/4',
	component:Yes,
},{
	path:'/5',
	component:Bm,
},{
	path:'/6',
	component:Gywm,
},{
	path:'/7',
	component:Dl,
},{
	path:'/gwc',
	component:gwc,
},
]

var router = new VueRouter({
	routes
})

export default router


